class User {
    constructor(useraccount, password, username, role,id) {
      this.useraccount = useraccount || "";
      this.password = password || "";
      this.username = username || "";
      this.role = role || "";
      this.id=id||0;
    }
  }
  class Token {
    constructor(user,) {
      this.user = user || "";
     
    }
  }

export {Token,User,
  };