import styled from "styled-components";
import { useEffect, useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { useNavigate, r } from "react-router-dom";
import { getAll, getItemById, createItem, updateItem } from "../API/Api";
import { useAlert } from "react-alert";
const Container = styled.div`
  position: relative;
  width: 100%;
  height: calc(100vh - 110px);
  font-weight: 600;
  font-size: 22px;
  background: #faf8f6;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  align-items: top;
  padding: 2rem 0 0 0;
  flex-direction: column;
  @media screen and (max-width: 480px) {
    padding: 0;
  }
  
`;
const ButtonArea = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
`;
const Button = styled(Link)`
  text-decoration: none;
  width: 140px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  background: #529e28;
  margin: 0 1rem;
  box-shadow: 0px 0px 0px 0px rgba(25, 118, 210, 0.16);
  transition: box-shadow 0.1s;
  ${(props) =>
    props.color === "blue" &&
    `
background: #4e8cc6;
`}
  &:hover {
    box-shadow: 0px 0px 0px 8px rgba(25, 118, 210, 0.16);
  }
`;
const Label = styled.label`
box-shadow: 10px 0 7px -7px #cccccc;
padding: 5px 10px;
margin: 16px;
margin-top: 0;
color: #cb4832;
width: 7rem;
height: 2.9rem;
@media screen and (max-width: 480px) {
  margin-bottom: 0;
}
`
const Input = styled.input`
  border: 1px solid #dcdcdc;
  font-weight: 600;
  font-size: 22px;
  border-radius: 5px;
  padding: 0 10px;
  width: calc(100% - 10rem);
  height: 45px;
  ::placeholder {
    color: #dddddd;
  }
  @media screen and (max-width: 480px) {
    width: 90%;
    margin: auto;
  }
`;
const Input2 = styled(Input)`
  width: 10rem;
@media screen and (max-width: 480px) {
  margin: 0
  }
`
const Textarea = styled.textarea`
border: 1px solid #dcdcdc;
font-weight: 600;
font-size: 22px;
border-radius: 5px;
padding: 0 10px;
width: calc(100% - 10rem);
::placeholder {
  color: #dddddd;
}
@media screen and (max-width: 480px) {
  width: 90%;
  margin: auto;
}
`
const Form = styled.form`
width: 100%;
max-width: 580px;
 div {
  display:flex;
  padding: 0.5rem 0;
 }
 @media screen and (max-width: 480px) {
  div {
    flex-direction: column;
    div {
      div {
        flex-direction: row;
      }
    }
  }
 }
`
const FlexColumn = styled.div`
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
width: calc(100% - 10rem);
div {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  width: 100%;
}
@media screen and (max-width: 480px) {
  width: 100%;
  div {
  }
}
`
const Actor = styled.p`
text-align: right;
width: 7rem;
padding: 0 1rem 0 0;
`
const ProjectTitle = styled.p`
@media screen and (max-width: 480px) {
  padding: 0 1rem 0 1.5rem;
}
`
class voiceActorObj {
  constructor(voiceCharacter, voiceActor) {
    this.voiceCharacter = voiceCharacter || "";
    this.voiceActor = voiceActor || "";
  }
}
class eduStage {
  constructor(id) {
    this.id = id || 1;
  }
}
class grade {
  constructor(id) {
    this.id = id || 1;
  }
}
class eachClassLearnProject {
  constructor(
    id,
    learnProjectTitle,
    learnProjectName,
    learnProjectDescription,
    learnResourceLink,
    grade,
    eduStage,
    user
  ) {
    this.id = id || 0;
    this.learnProjectTitle = learnProjectTitle || "";
    this.learnProjectName = learnProjectName || "";
    this.learnProjectDescription = learnProjectDescription || "";
    this.learnResourceLink = learnResourceLink || "";
    this.grade = grade || 1;
    this.eduStage = eduStage || 1;
    this.voiceActors = [];
    this.user = user;
  }
  addVoiceActor(voiceActor) {
    this.voiceActors.push(voiceActor);
  }
}
function NewProjectBody(props) {
  const alert = useAlert();
  const apiUrl = process.env.REACT_APP_API_URL;
  const { currentProject, setCurrentProject } = props;
  const [btn_add, setbtn_add] = useState("add");
  const navigate = useNavigate();
  const [voiceActorObjs, setVpoiceAcorObjs] = useState([]);
  const [selectedGrade, setSelectedGrade] = useState(1);
  const [selectedEduStage, setSelectedEduStage] = useState(1);
  const [learnProjectName, setLearnProjectName] = useState("");
  const [learnProjectDescription, setLearnProjectDescription] = useState("");
  const [learnProjectLink, setLearnProjectLink] = useState("");
  const [learnProjectTitle, setLearnProjectTitle] =
    useState("B11L4:()");

  const [voiceActor1, setvoiceActor1] = useState("");
  const [voiceActor2, setvoiceActor2] = useState("");
  const [voiceActor3, setvoiceActor3] = useState("");
  useEffect(() => {
    //   fetchGetPorject(currentProject);
    console.log("1-useEffect:");
    if (
      window.localStorage.getItem("currentProject") &&
      window.localStorage.getItem("currentProject") != "undefined"
    ) {
      setCurrentProject(
        JSON.parse(window.localStorage.getItem("currentProject"))
      );
    }

    //update
    if (currentProject) {
      console.log(" id :" + currentProject.id);
      setbtn_add("");
      let proj = getItemById("eachClassLearnProject", currentProject.id)
        .then((response) => {
          console.log(
            "eachClassLearnProject:" + JSON.stringify(response.content)
          );

          setCurrentProject(response);
          setLearnProjectName(response.learnProjectName);
          setLearnProjectDescription(response.learnProjectDescription);
          setLearnProjectLink(response.learnResourceLink);
          if (response.voiceActors) {
            var _voiceActors = response.voiceActors;
            setVpoiceAcorObjs(_voiceActors);
            const _VoiceActor1 = _voiceActors.find(
              (actor) => actor.voiceCharacter === ""
            );
            const _VoiceActor2 = _voiceActors.find(
              (actor) => actor.voiceCharacter === ""
            );
            const _VoiceActor3 = _voiceActors.find(
              (actor) => actor.voiceCharacter === ""
            );
            setvoiceActor1(_VoiceActor1.voiceActor);
            setvoiceActor2(_VoiceActor2.voiceActor);
            setvoiceActor3(_VoiceActor3.voiceActor);
          }
          // Process the created item
        })
        .catch((error) => {
          alert.error(":" + error);
          console.error("Error:", error);
          // Handle any errors
        });
    }
  }, []);

  //select list handler
  const handleGradeSelectChange = (event) => {
    const selectedValue = event.target.value;
    setSelectedGrade(selectedValue);
    // 
  };
  const handleEduStageSelectChange = (event) => {
    const selectedValue = event.target.value;
    setSelectedEduStage(selectedValue);
    // 
  };
  const handleSubmit = () => {
    // 
  };
  const saveData = () => {
    if (
      learnProjectName === "" ||
      learnProjectDescription === "" ||
      selectedGrade === "" ||
      selectedEduStage === "" ||
      voiceActor1 === "" ||
      voiceActor2 === "" ||
      voiceActor3 === ""
    ) {
      alert.error("");
      return;
    }
    var el_project = new eachClassLearnProject(
      0,
      learnProjectTitle,
      learnProjectName,
      learnProjectDescription,
      learnProjectLink,
      selectedGrade,
      selectedEduStage,
      selectedGrade,
      selectedEduStage
    );

    let _user = JSON.parse(window.localStorage.getItem("user"));

    //
    if (
      currentProject &&
      currentProject.id !== null &&
      currentProject.id != 0
    ) {
      el_project.eduStage = new eduStage(selectedEduStage);
      el_project.grade = new grade(selectedGrade);
      el_project.id = currentProject.id;
      const _VoiceActor1 = voiceActorObjs.find(
        (actor) => actor.voiceCharacter === ""
      );
      const _VoiceActor2 = voiceActorObjs.find(
        (actor) => actor.voiceCharacter === ""
      );
      const _VoiceActor3 = voiceActorObjs.find(
        (actor) => actor.voiceCharacter === ""
      );
      _VoiceActor1.voiceActor = voiceActor1;
      el_project.addVoiceActor(_VoiceActor1);
      _VoiceActor2.voiceActor = voiceActor2;
      el_project.addVoiceActor(_VoiceActor2);
      _VoiceActor3.voiceActor = voiceActor3;
      el_project.addVoiceActor(_VoiceActor3);
      let proj = updateItem(
        "eachClassLearnProject",
        currentProject.id,
        el_project
      )
        .then((response) => {
          alert.success("!");
        })
        .catch((error) => {
          alert.error(":" + error);
          console.error("Error:", error);
          // Handle any errors
        });
    } else {
      const _VoiceActor1 = new voiceActorObj("", voiceActor1);
      el_project.addVoiceActor(_VoiceActor1);
      const _VoiceActor2 = new voiceActorObj("", voiceActor2);
      el_project.addVoiceActor(_VoiceActor2);

      const _VoiceActor3 = new voiceActorObj("", voiceActor3);
      el_project.addVoiceActor(_VoiceActor3);
      // el_project.addEachClassLearnProject
      el_project.eduStage = new eduStage(selectedEduStage);
      el_project.grade = new grade(selectedGrade);
      let proj = createItem("eachClassLearnProject", el_project)
        .then((response) => {
          alert.success("!");
          console.log("data:" + JSON.stringify(response));
          setCurrentProject(response);
          window.localStorage.setItem(
            "currentProject",
            JSON.stringify(response)
          );
          setbtn_add("");
        })
        .catch((error) => {
          console.error("Error:", error);
          // Handle any errors
        });
    }
    // 
  };

  const toRecord = (e) => {
    const i = Number(e.target.id);
    window.localStorage.setItem(
      "currentProject",
      JSON.stringify(currentProject)
    );
    navigate("/Recording");
  };

  return (
    <Container>
      <Form onSubmit={handleSubmit}>
        <div>
          <Label></Label>
          <ProjectTitle>{
            learnProjectTitle
              ? learnProjectTitle
              : "B11L4:()"
          }</ProjectTitle>
        </div>
        <div>
          <Label></Label>
          <Input
            type="text"
            placeholder="32:A--"
            value={learnProjectName}
            required
            onChange={(e) => setLearnProjectName(e.target.value)}
          />
        </div>
        <div>
          <Label></Label>
          <Textarea rows="4" 
            placeholder="-A(1)"
            value={learnProjectDescription}
            required="required"
            onChange={(e) => setLearnProjectDescription(e.target.value)}
          />
        </div>
        <div>
          <Label></Label>
          <FlexColumn>
            <div>
              <Actor></Actor>
              <Input2
                placeholder=""
                value={voiceActor1}
                required="required"
                onChange={(e) => setvoiceActor1(e.target.value)}
              />
            </div>
            <div>
              <Actor></Actor>
              <Input2
                placeholder=""
                value={voiceActor2}
                required="required"
                onChange={(e) => setvoiceActor2(e.target.value)}
              />
            </div>
            <div>
              <Actor></Actor>
              <Input2
                placeholder=""
                value={voiceActor3}
                required="required"
                onChange={(e) => setvoiceActor3(e.target.value)}
              />
            </div>
          </FlexColumn>
        </div>
      </Form>
      <ButtonArea>
        <Button onClick={saveData} title=""></Button>
        <Button add={btn_add} color="blue" to="/Recording" title=""></Button>
      </ButtonArea>
    </Container>
  );
}

export default NewProjectBody;
