import styled from "styled-components";
import Header from "./components/Header";
import { useState, useEffect } from "react";
import { User, Token } from "./components/Auth";
import { useNavigate } from "react-router-dom";
import { useAlert } from "react-alert";
import { login } from "./API/AuthApi";

const Container = styled.div`
  width: 100%;
`;
const LoginContainer = styled.form`
  margin: auto;
  padding: 2rem 0;
  width: 10rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: ${22/16}rem;
  button {
    width: 94px;
    height: 50px;
    margin-top: 2.5rem;
    border: none;
    border-radius: 5px;
    color: #FFF;
    background: #529e28;
    margin-right: 72px; 
    :hover {
      background: #58aa2c;
    }
  }
  div {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    label {
      color: #cb4832;
      width: 3.5rem;
      margin-right: 1rem;
      box-shadow: 7px 0 5px -5px rgba(0,0,0,0.3);
    }
    input {
      width: 240px;
      height: 45px;
      border-radius: 5px;
      border: 1px solid #cccccc;
      padding: 0 10px;
    }
  }
`;

function LoginPage(props) {
  const { isLoggedIn, setIsLoggedIn, user, setUser } = props;
  const alert = useAlert();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();
  const url = "";

  useEffect(()=>{
    if(isLoggedIn)
    {
      navigate("/home");
    }
  },[isLoggedIn]);
  const handleLogin = (e) => {
    e.preventDefault()
    //if()
    if (username && password) {
      //window.localStorage.setItem("token", token);
      let proj = login(new User(username, password))
        .then((response) => {
          if (response) {
            setIsLoggedIn(true);
            let rep_user= JSON.stringify(response);
            setUser(new User(rep_user.useraccount, null, rep_user.username, null,rep_user.id));

            console.log("data:" + JSON.stringify(response));

            window.localStorage.setItem("user", JSON.stringify(response));
          
            navigate("/home");
            
          } else {
            alert.error("!");
          }

        })
        .catch((error) => {
          console.error("Error:", error);
          alert.error("!");
          // Handle any errors
        });

    } else {
      alert.error("!");
    }
  };
  return (
    <Container>
      <Header
        isLoggedIn={isLoggedIn}
        setLoggedIn={setIsLoggedIn}
        setUser={setUser}
        type='login'
      />
      <LoginContainer>
        <div>
          <label for="username"></label>
          <input
            name="username"
            type="text"
            onChange={(e) => setUsername(e.target.value)}
            value={username}
          ></input>
        </div>
        <div>
          <label for="password"></label>
          <input
            name="password"
            type="password"
            onChange={(e) => setPassword(e.target.value)}
            value={password}
          ></input>
        </div>
        <button onClick={handleLogin}></button>
      </LoginContainer>
    </Container>
  );
}

export default LoginPage;
